.containerchat {
    width: 400px;
    height: 80vh;
    background-color: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.6);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    position:absolute;
    bottom:40px;
    right:40px;
    z-index: 10000; 
    transition: all 0.5s ease;
    overflow: hidden;
  }
  
  .containerchat.expanded {
    width: 400px;
    height: 80vh;
    border-radius: 8px;
  }
  
  .containerchat.collapsed {
    width: 50px; /* Adjust size for the circle */
    height: 50px; /* Adjust size for the circle */
    border-radius: 50%;
    cursor: pointer;
    z-index: 5000;
  }
  
  .nav-barchat {
    width: 100%;
    height: 40px;
    background-color: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 20px;
    animation: fadeIn 0.5s forwards;
    animation-delay: 0.7s;
  }
  
  .nav-barchat a {
    color: black;
    white-space: nowrap;
    margin-left: 10px;
    user-select: none;
    animation: fadeIn 0.5s forwards;
    animation-delay: 0.7s;
  }
  
  .close {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    cursor: pointer;
    animation: fadeIn 0.5s forwards;
  animation-delay: 0.7s;
  }
  
  .line {
    position: absolute;
    width: 20px;
    height: 3px;
    background-color: #6B6C7B;
    border-radius: 30px;
  }
  
  .line.one {
    transform: rotate(45deg)
  }
  
  .line.two {
    transform: rotate(135deg)
  }
  
  .messages-area {
    width: 100%;
    height: 80%;
    padding:10px;
    display:flex;
    flex-direction: column;
    gap:10px;
    overflow-y: hidden;
    position: relative;
    z-index: 20000;
    background: none;
    animation: fadeIn 0.5s forwards;
  animation-delay: 0.9s;
  overflow-x: hidden;
  }
  
  #chats{
    width: 100%;
    height: 100%;
    padding:10px;
    display:flex;
    flex-direction: column;
    gap:10px;
    overflow-y: scroll;
    position: relative;
    overflow-x: hidden;
  }
  
  #chats::-webkit-scrollbar {
    display: none;
  }
  
  .sender-area {
    background-color: none;
    width: 400px;
    height: 70px;
    display: flex;
    border-radius: 8px;
    position:absolute;
    bottom:5px;
    animation: fadeIn 0.5s forwards;
  animation-delay: 1.1s;
  animation: fadeIn 0.5s forwards;
    animation-delay: 0.9s;
  }
  
  .send-img {
    width: 30px;
  }
  
  .send-input {
    outline: none;
    display: flex;
    border: none;
    background: none;
    height: 40px;
    width: 350px;
    border-radius: 7px;
    background: none;
    color: black;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    margin-left: 5px;
  }
  
  .send-input::placeholder {
    color: #6b6b6b;
  }
  
  .input-place {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: row;
    margin-top: 15px;
    margin-left: 10px;
    align-items: center;
    background-color: white;
    border-radius: 20px;
    height: 40px;
    width: 93%;
    gap: 5px;
    padding:5px;
    border: 1px solid none;
    transition: opacity 0.5s ease;
  }
  
  .containerchat.expanded .input-place {
    opacity: 1;
    animation: fadeIn 0.5s forwards;
  }
  
  .containerchat.collapsed .input-place {
    opacity: 0;
    animation: fadeOut 0.5s forwards;
  }
  
  .containerchat.expanded .nav-barchat a {
    opacity: 1;
    animation: fadeIn 0.5s forwards;
  }
  
  .containerchat.collapsed .nav-barchat a {
    opacity: 0;
    animation: fadeOut 0.5s forwards;
  }
  
  @keyframes fadeOut {
    from {
      opacity: 1;
    }
    to {
      opacity: 0;
    }
  }
  
  .input-place button{
    background: none;
    border: none;
  }
  
  .send {
    width: 30px;
    height: 30px;
    background-color: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  
  .send-icon {
    width: 17px;
  }
  
  #chatbubble{
    width:fit-content;
    max-width: 350px;
    height:fit-content;
    padding:10px;
    border-radius: 20px;
    z-index: 100;
  }
  
  #chatbubble p {
    margin: 0;
    padding: 0;
  }
  
  #chatbubble h1,
  #chatbubble h2,
  #chatbubble h3,
  #chatbubble h4,
  #chatbubble h5,
  #chatbubble h6 {
    margin: 5px 0;
  }
  
  #chatbubble ul,
  #chatbubble ol {
    margin: 5px 0;
    padding-left: 20px;
  }
  
  #chatbubble a {
    color: #fff;
    text-decoration: none;
    font-style: italic;
  }
  
  #chatbubble a:hover {
    text-decoration: underline;
  }
  
  #chatbubble blockquote {
    margin: 5px 0;
    padding: 10px;
    background-color: #f5afaf;
    border-left: 5px solid #c43f3f;
  }
  
  #chatbubble code {
    display: inline-block;
    background-color: #e5a19b;
    padding: 4px 6px;
    border-radius: 8px;
    font-family: monospace;
    font-size: 0.95rem;
    white-space: pre-wrap;
    word-wrap: break-word;
  }
  
  .user{
    margin-left: auto;
    background:  #4f29f0;
    color:white;
  }
  
  .chatbot{
    margin-left: 0;
    background:  #ed7d72;
    color:white;
  }
  
  #circle1{
    height:400px;
    width:400px;
    background:#8267fd;
    z-index: 5;
    border-radius: 50%;
    position: absolute;
    bottom:-40%;
    left:-10%;
    opacity:0.1;
    animation: fadeIn2 0.5s forwards;
    animation-delay: 0.7s;
  }
  
  #circle2{
    height:500px;
    width:500px;
    z-index: 9;
    border-radius: 50%;
    position: absolute;
    top:-20%;
    right:-40%;
    background:#ed7d72;
    opacity: 0.1;
    animation: fadeIn2 0.5s forwards;
    animation-delay: 0.7s;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  @keyframes fadeIn2 {
    from {
      opacity: 0;
    }
    to {
      opacity: 0.1;
    }
  }
  
  .containerchat.collapsed:hover{
    transform: scale(1.1);
    content: "Need Help?";
  }
  
  .containerchat.collapsed{
    background: url('../../../Assets/chatbot.avif');
    z-index: 99999;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }